/* Colors */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Roboto"), url("../fonts/roboto.woff2") format("woff2"), url("../fonts/roboto.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Roboto-Medium"), url("../fonts/robotomedium.woff2") format("woff2"), url("../fonts/robotomedium.woff") format("woff");
}

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Roboto-Bold"), url("../fonts/robotobold.woff2") format("woff2"), url("../fonts/robotobold.woff") format("woff");
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  color: #ffffff;
  background-color: #849d8f;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  min-width: 900px;
  max-width: 1200px;
  margin: 0 auto;
}

.container {
  padding: 0 2.3%;
}

.page-inner {
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.3) 0%, transparent 100%);
  background-size: 100% 660px;
  background-position: 0 -100px;
  background-repeat: no-repeat;
}

.button {
  position: relative;
  display: block;
  padding: 8px 10px 12px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  text-align: center;
  border: none;
  border-radius: 22px;
  color: #ffffff;
  background-image: linear-gradient(to bottom, #f26843 0%, #e74a35 100%);
  box-shadow: 0 2px 2px 0 rgba(172, 16, 0, 0.64);
  cursor: pointer;
}

.button::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 22px;
}

.button:hover {
  background-color: rgba(255, 255, 255, 0.2);
  box-shadow: 0 2px 2px 0 #ac1000;
}

.button:hover::before {
  background-color: rgba(255, 255, 255, 0.2);
}

.button:active {
  outline: none;
  background-image: linear-gradient(to bottom, #e74a35 0%, #f26843 100%);
  box-shadow: inset 0 2px 2px 0 #942718;
}

.button:active::before {
  background-color: rgba(0, 0, 0, 0.07);
}

.button--big {
  padding: 8px 32px 12px;
  font-size: 32px;
  line-height: 44px;
  border-radius: 32px;
  text-shadow: 0 2px 5px rgba(172, 16, 0, 0.64);
}

.button--big::before {
  border-radius: 32px;
}

.button--light {
  padding: 9px 19px 10px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0 0 0 2px #ffffff;
}

.button--light:hover {
  color: #333333;
  background-color: #ffffff;
  box-shadow: inset 0 0 0 2px #ffffff;
}

.button--light:active {
  color: #333333;
  background: #ededed;
  box-shadow: inset 0 2px 1px 0 #696969;
}

.page-header__wrapper {
  display: flex;
  align-items: center;
  padding-top: 5px;
}

.page-header__logo {
  flex-shrink: 0;
  margin-top: 3px;
  margin-right: 9px;
}

.page-header__main-navigation {
  margin-right: auto;
}

.page-header__search {
  margin-right: 10px;
}

.logo {
  display: block;
  font-size: 0;
}

.logo:hover[href] {
  opacity: 0.75;
}

.logo:active[href] {
  opacity: 0.5;
}

.logo-image {
  width: 154px;
  height: 62px;
}

.popup-menu {
  position: absolute;
  top: 115%;
  z-index: -1;
  opacity: 0;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.4);
}

.popup-menu::before {
  position: absolute;
  top: -10px;
  left: 0;
  content: "";
  width: 100%;
  height: 10px;
}

.popup-menu--left {
  left: -6px;
}

.popup-menu--right {
  right: -2px;
}

.main-navigation__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-navigation__item {
  position: relative;
  padding: 7px 14px;
}

.main-navigation__item:hover, .main-navigation__item:focus {
  color: #333333;
  background-color: #f8f7f4;
  border-radius: 16px;
}

.main-navigation__item:hover .main-navigation__submenu, .main-navigation__item:focus .main-navigation__submenu {
  opacity: 1;
  z-index: 1;
}

.main-navigation__item:hover .main-navigation__link, .main-navigation__item:focus .main-navigation__link {
  outline: none;
  box-shadow: none;
}

.main-navigation__item:focus-within {
  color: #333333;
  background-color: #f8f7f4;
  border-radius: 16px;
}

.main-navigation__item:focus-within .main-navigation__submenu {
  opacity: 1;
  z-index: 1;
}

.main-navigation__item:focus-within .main-navigation__link {
  outline: none;
  box-shadow: none;
}

.main-navigation__item--current {
  color: #ffffff;
  background-color: #d07058;
  border-radius: 16px;
}

.main-navigation__item--current .main-navigation__link {
  box-shadow: none;
}

.main-navigation__link {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 255, 255, 0.2);
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}

.submenu {
  margin: 0;
  padding: 5px 0;
  min-width: 143px;
  list-style: none;
  background-color: #f8f7f4;
  border-radius: 5px;
}

.submenu__link {
  display: block;
  padding: 6px 20px;
  margin-bottom: 3px;
  font-size: 14px;
  line-height: 16px;
}

.submenu__link:hover[href] {
  background-color: #fbded7;
}

.submenu__link:active[href] {
  background-color: #f6b5a5;
}

.submenu__link--special {
  position: relative;
  padding: 6px 20px;
  margin-bottom: 10px;
  font-weight: 700;
}

.submenu__link--special::before {
  position: absolute;
  top: 33px;
  left: 6px;
  content: "";
  width: 128px;
  height: 1px;
  background-color: #d3d3d2;
}

.submenu__link--active {
  color: #ffffff;
  background-color: #d07058;
}

.input {
  width: 100%;
  padding: 9px 14px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  color: #333333;
  background-color: #ffffff;
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 0 1px #d3d3d2;
}

.input::placeholder {
  font-size: 14px;
  font-weight: 400;
  color: #999999;
}

.input:hover {
  box-shadow: 0 0 0 2px #c7c6c5;
}

.input:focus {
  outline: none;
  box-shadow: 0 0 0 2px #8fbdec;
}

.search {
  position: relative;
}

.search:hover .search__wrapper, .search:focus .search__wrapper {
  opacity: 1;
  z-index: 1;
}

.search:focus-within .search__wrapper {
  opacity: 1;
  z-index: 1;
}

.search__icon {
  fill: #ffffff;
}

.search__label {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33px;
  height: 33px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
}

.search__label:hover {
  background-color: #f8f7f4;
  cursor: pointer;
}

.search__label:hover .search__icon {
  fill: #000000;
}

.search__wrapper {
  display: block;
  width: 341px;
  height: 84px;
  padding: 20px 15px;
  background-color: #f8f7f4;
  border-radius: 5px;
  cursor: auto;
}

.user-navigation {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.user-navigation__item {
  position: relative;
}

.user-navigation__item:not(:last-child) {
  margin-right: 9px;
}

.user-navigation__link {
  position: relative;
  display: block;
  padding: 8px 16px 8px 45px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
}

.user-navigation__link:hover {
  color: #333333;
  background-color: #f8f7f4;
}

.user-navigation__link:hover .user-navigation__icon {
  fill: #000000;
}

.user-navigation__link--wide {
  min-width: 117px;
}

.user-navigation__link--cart {
  color: #333333;
  background-color: #f8f7f4;
}

.user-navigation__link--cart .user-navigation__icon {
  fill: #000000;
}

.user-navigation__icon {
  position: absolute;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
  fill: #ffffff;
}

.user-navigation__item--login:hover .user-navigation__form,
.user-navigation__item--login:focus .user-navigation__form {
  opacity: 1;
  z-index: 1;
}

.user-navigation__item--login:focus-within .user-navigation__form {
  opacity: 1;
  z-index: 1;
}

.user-navigation__item--cart:hover .user-navigation__cart,
.user-navigation__item--cart:focus .user-navigation__cart {
  opacity: 1;
  z-index: 1;
}

.user-navigation__item--cart:focus-within .user-navigation__cart {
  opacity: 1;
  z-index: 1;
}

.login {
  width: 277px;
  height: 214px;
  padding: 21px 18px 22px 20px;
  background-color: #f8f7f4;
  border-radius: 5px;
}

.login::before {
  position: absolute;
  top: -10px;
  left: 0;
  content: "";
  width: 100%;
  height: 10px;
}

.login__input {
  margin-bottom: 22px;
}

.login__wrapper {
  display: flex;
  justify-content: space-between;
}

.login__button {
  align-self: flex-start;
  width: 100px;
}

.login__links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 2px;
}

.login__link {
  display: inline;
  box-shadow: inset 0 -1px rgba(0, 0, 0, 0.3);
  color: #333333;
  font-size: 13px;
  line-height: 16px;
  color: #333333;
}

.login__link:hover {
  display: inline;
  box-shadow: inset 0 -1px rgba(232, 77, 55, 0.3);
  color: #e84d37;
}

.login__link:active {
  display: inline;
  box-shadow: inset 0 -1px rgba(232, 77, 55, 0.3);
  color: #e84d37;
  opacity: 0.7;
}

.login__link:not(:last-child) {
  margin-bottom: 5px;
}

.cart {
  width: 540px;
  padding: 20px 15px;
  color: #333333;
  background-color: #f8f7f4;
  border-radius: 5px;
}

.cart__table {
  width: 510px;
  border-collapse: collapse;
}

.cart__row {
  margin-bottom: 10px;
}

.cart__cell {
  margin: 0;
  padding-bottom: 20px;
  font-size: 13px;
  line-height: 16px;
  text-align: left;
  vertical-align: top;
}

.cart__cell-1 {
  padding-top: 9px;
  width: 27px;
}

.cart__cell-2 {
  width: 33px;
}

.cart__cell-3 {
  padding: 8px 11px 20px;
  width: 250px;
}

.cart__cell-4 {
  padding: 8px 8px 20px;
  width: 120px;
}

.cart__cell-5 {
  padding: 8px 11px 20px;
  width: 80px;
}

.cart__delete {
  display: block;
  width: 11px;
  height: 11px;
  font-size: 0;
  background: url("../img/x-icon.svg") no-repeat;
}

.cart__thumb {
  display: block;
  width: 33px;
  height: 33px;
}

.cart__item {
  margin-bottom: 10px;
}

.cart__unit-price {
  color: #999999;
}

.cart__fullprice {
  padding: 15px 0;
  font-size: 15px;
  line-height: 18px;
  font-weight: 700;
  text-align: right;
  border-top: 1px solid #d3d3d2;
}

.cart__button {
  margin-left: auto;
  width: 170px;
}

.telephone {
  margin: 0;
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  text-align: right;
}

.telephone__time {
  display: block;
  margin-bottom: 2px;
  font-size: 14px;
  line-height: 16px;
}

.page-main__slider {
  margin-bottom: 41px;
}

.page-main__promo {
  margin-bottom: 31px;
}

.page-main__catalog {
  margin-bottom: 28px;
}

.page-main__about {
  margin-bottom: 40px;
}

.page-main__row {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 39px;
}

.page-main__contacts {
  overflow: hidden;
}

.slider {
  position: relative;
}

.slider--nojs .slider__controls {
  display: none;
}

.slider__controls {
  position: absolute;
  bottom: 22px;
  left: 0;
  z-index: 1;
  display: flex;
}

.slider__dot {
  display: block;
  width: 21px;
  height: 21px;
  background-color: transparent;
  border: 2px solid #ffffff;
  border-radius: 50%;
  cursor: pointer;
}

.slider__dot:not(:last-child) {
  margin-right: 8px;
}

.slider__dot:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.slider__dot:active {
  background-color: #ffffff;
}

.slider__dot--active {
  background-color: #ffffff;
}

.slider__slide-list {
  margin: 0 -2.411%;
  padding: 0;
  list-style: none;
}

.slider__slide {
  display: none;
  width: 100%;
}

.slider__slide--show {
  display: block;
}

.slide {
  position: relative;
  padding-top: 274px;
}

.slide__box {
  position: absolute;
  top: -116px;
  z-index: -1;
  width: 100%;
}

.slide__image {
  opacity: 0;
}

.slide__image--1 {
  background: #849d8f url("../img/slide-1.png") no-repeat center top;
}

.slide__image--2 {
  background: #8996a6 url("../img/slide-2.png") no-repeat center top;
}

.slide__image--3 {
  background: #9d8b84 url("../img/slide-3.png") no-repeat center top;
}

.slide__title {
  margin: 0 0 32px;
  font-size: 60px;
  line-height: 60px;
  font-weight: 700;
  text-align: center;
}

.slide__link {
  margin: 0 auto;
  width: 280px;
}

.promo__list {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  list-style: none;
}

.promo__item {
  display: flex;
  flex-direction: column;
  width: 48.8%;
  min-height: 229px;
  padding: 15px 19px 24px;
  border-radius: 15px;
}

.promo__item--raspberry {
  background: #9f5367 url("../img/raspberry.jpg") repeat center;
}

.promo__item--chocolate {
  background: #4c352b url("../img/chocolate.jpg") repeat center;
}

.promo__slogan {
  margin-bottom: 15px;
  font-size: 35px;
  line-height: 41px;
  font-weight: 700;
}

.promo__description {
  margin: 0 0 auto;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
}

.promo__button {
  align-self: flex-end;
  padding-left: 22px;
  padding-right: 22px;
}

.catalog {
  margin-left: 1.15%;
  margin-right: 1.15%;
}

.catalog__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 -19px;
  padding: 0;
  list-style: none;
}

.catalog__item {
  width: 25%;
  margin-bottom: 19px;
}

.product {
  position: relative;
  text-align: center;
}

.product:hover .product__hover {
  opacity: 1;
  z-index: 1;
}

.product--hit::before {
  position: absolute;
  top: 3%;
  left: 4.4%;
  z-index: 1;
  content: "";
  width: 61px;
  height: 61px;
  background: #ffffff url("../img/hit.svg") no-repeat center;
  border-radius: 50%;
}

.product__box {
  padding: 3% 4.4% 10px;
}

.product__wrapper {
  position: relative;
  margin-bottom: 13px;
}

.product__image {
  display: block;
  min-width: 200px;
  height: auto;
}

.product__price {
  position: absolute;
  bottom: 8px;
  left: 0;
  font-size: 45px;
  line-height: 45px;
  font-weight: 700;
  text-shadow: 0 1px 3px rgba(49, 50, 53, 0.5);
}

.product__small {
  font-size: 30px;
}

.product__name {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 255, 255, 0.2);
  margin: 0;
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
}

.product__hover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  height: 120%;
  width: 100%;
  padding-bottom: 7.7%;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  box-shadow: 0 20px 20px 0 rgba(0, 0, 0, 0.4);
  opacity: 0;
}

.product__hover--show {
  opacity: 1;
  z-index: 1;
}

.product__link {
  margin-top: auto;
  width: 200px;
}

.about {
  padding: 25px 20px 14px;
  color: #333333;
  background: #f0e6cf url("../img/wafer.jpg");
  border-radius: 15px;
}

.about__title {
  margin: 0 0 9px;
  font-size: 24px;
  line-height: 30px;
  font-weight: 500;
}

.about__box {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.about__description {
  margin: 0;
  padding: 12px 0 10px 56px;
  width: 48.8%;
  font-size: 16px;
  line-height: 22px;
}

.about__description--1 {
  background: url("../img/icecream.svg") no-repeat 0 0;
}

.about__description--2 {
  background: url("../img/leaf.svg") no-repeat 0 0;
}

.about__description--3 {
  background: url("../img/cow.svg") no-repeat 0 0;
}

.about__description--4 {
  background: url("../img/thermometer.svg") no-repeat 0 0;
}

.blog {
  width: 48.8%;
  min-height: 220px;
  padding: 25px 20px;
  color: #5a5a5a;
  border-radius: 15px;
}

.blog--1 {
  background: #ffffff url("../img/strawberry.jpg") no-repeat right 0;
}

.blog__title {
  margin: 0;
  font-size: 16px;
  line-height: 22px;
  font-weight: 500;
}

.blog__link {
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  text-decoration: underline;
  background-color: rgba(255, 255, 255, 0.8);
}

.blog__link:hover {
  color: #e84d37;
}

.blog__link:active {
  color: #e84d37;
  opacity: 0.7;
}

.news {
  display: flex;
  width: 48.8%;
  min-height: 220px;
  padding: 5px;
  background-image: linear-gradient(to right bottom, #f8f7f4 12.5%, #92cdff 12.5%, #92cdff 25%, #f8f7f4 25%, #f8f7f4 37.5%, #fe9e90 37.5%, #fe9e90 50%, #f8f7f4 50%, #f8f7f4 62.5%, #92cdff 62.5%, #92cdff 75%, #f8f7f4 75%, #f8f7f4 87.5%, #fe9e90 87.5%, #fe9e90 100%);
  background-size: 40px 40px;
  border-radius: 15px;
}

.news__inner {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 30px 21px 35px 21px;
  background-color: #f8f7f4;
  border-radius: 10px;
}

.news__description {
  margin: 0 0 35px;
  font-size: 16px;
  line-height: 22px;
  color: #5a5a5a;
}

.news__form {
  display: flex;
}

.news__input {
  margin-right: 11px;
}

.news__input::placeholder {
  font-size: 16px;
  line-height: 24px;
}

.news__button {
  width: 129px;
  flex-shrink: 0;
}

.contacts {
  position: relative;
  min-height: 430px;
}

.contacts__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  background: #dddddd url("../img/map.jpg") no-repeat 25% top;
}

.contacts__map {
  position: relative;
  z-index: 1;
  display: block;
  opacity: 0;
}

.contacts__frame {
  position: absolute;
  top: 0;
  left: 0;
  border: none;
}

.contacts__block {
  position: absolute;
  z-index: 3;
  top: 50%;
  right: 2.3%;
  transform: translateY(-50%);
  width: 302px;
  padding: 30px 25px;
  color: #333333;
  background-color: #ffffff;
  border-radius: 15px;
}

.contacts__title {
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}

.contacts__address {
  margin-bottom: 18px;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  font-style: normal;
}

.contacts__phone {
  margin: 0 0 24px;
  font-size: 14px;
  line-height: 20px;
}

.contacts__telephone {
  display: block;
  margin: 4px 0;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
}

.page-footer {
