  justify-content: space-between;
  padding-bottom: 30px;
}

.page-footer__social {
  margin: 36px 0 0;
}

.page-footer__list {
  margin: 34px auto 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  width: 300px;
}

.page-footer__item {
  width: 142px;
}

.page-footer__link {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 255, 255, 0.2);
  color: #ffffff;
  font-size: 13px;
  line-height: 18px;
}

.page-footer__link:hover {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 188, 158, 0.3);
  color: #f9ac9f;
}

.page-footer__link:active {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 188, 158, 0.3);
  color: #f9ac9f;
  opacity: 0.7;
}

.page-footer__link--special {
  position: relative;
  font-weight: 700;
}

.page-footer__link--special::before {
  position: absolute;
  top: 50%;
  left: -22px;
  transform: translateY(-50%);
  content: "";
  width: 15px;
  height: 13px;
  background: url("../img/heart.svg");
}

.page-footer__developer {
