}

.social {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 0;
  list-style: none;
}

.social__link {
  display: block;
  height: 38px;
}

.social__item:not(:last-child) {
  margin-right: 12px;
}

.social__icon {
  width: 38px;
  height: 38px;
  opacity: 0.8;
  border: 3px solid rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  box-shadow: inset 0 0 0 3px rgba(255, 255, 255, 0.5);
}

.social__icon:hover {
  opacity: 1;
  border-color: rgba(255, 255, 255, 0.7);
}

.social__icon:active {
  opacity: 0.6;
  border-color: rgba(255, 255, 255, 0.7);
}

.developer {
  font-size: 12px;
  line-height: 18px;
}

.developer__link {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 255, 255, 0.2);
  color: #ffffff;
}

.developer__link:hover {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 188, 158, 0.3);
  color: #f9ac9f;
}

.developer__link:active {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 188, 158, 0.3);
  color: #f9ac9f;
  opacity: 0.7;
}

.developer__logo {
  display: block;
  margin-bottom: 5px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  display: none;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
}

.modal--show {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal__inner {
  position: relative;
}

.modal__inner--appear {
  animation-name: modal--appear;
  animation-duration: 0.5s;
  animation-timing-function: ease-out;
}

@keyframes modal--appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.modal__close {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 3;
  width: 18px;
  height: 17px;
  padding: 0;
  background: url("../img/x-big-icon.svg") no-repeat;
  border: none;
  cursor: pointer;
}

.form-modal {
  width: 477px;
  padding: 15px 25px 30px;
  background-color: #f8f7f4;
  border-radius: 10px;
}

.form-modal--invalid {
  animation-name: form-invalid;
  animation-duration: 100ms;
  animation-iteration-count: 5;
}

@keyframes form-invalid {
  33% {
    transform: translateX(-10px);
  }
  66% {
    transform: translateX(10px);
  }
}

.form-modal__title {
  margin: 0 0 20px;
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  color: #333333;
}

.form-modal__input {
  width: 265px;
  margin-bottom: 22px;
}

.form-modal__input::placeholder {
  font-size: 16px;
  line-height: 24px;
}

.form-modal__input--big {
  display: block;
  margin-bottom: 25px;
  width: 100%;
  min-height: 153px;
  resize: none;
}

.form-modal__submit {
  margin-left: auto;
  width: 140px;
}

.catalog-main__telephone {
  margin-bottom: -20px;
}

.catalog-main__title {
  margin: 3px 0 28px;
  font-size: 35px;
  line-height: 41px;
  font-weight: 700;
}

.catalog-main__filter {
  margin-bottom: 14px;
}

.catalog-main__catalog {
  margin-bottom: 72px;
}

.catalog-main__pagination {
  padding-bottom: 30px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.breadcrumbs {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.breadcrumbs__item {
  margin-right: 21px;
}

.breadcrumbs__link {
  font-size: 14px;
  line-height: 16px;
}

.breadcrumbs__link[href] {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 255, 255, 0.2);
  color: #ffffff;
  position: relative;
}

.breadcrumbs__link[href]:hover {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 188, 158, 0.3);
  color: #f9ac9f;
}

.breadcrumbs__link[href]:active {
  display: inline;
  box-shadow: inset 0 -1px rgba(255, 188, 158, 0.3);
  color: #f9ac9f;
  opacity: 0.7;
}

.breadcrumbs__link[href]::after {
  position: absolute;
  top: 1px;
  right: -14px;
  content: "\00BB";
}

.breadcrumbs__link[href]:hover::after, .breadcrumbs__link[href]:active::after {
  color: #ffffff;
}

.filter__form {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.filter__sort {
  margin: 0 14px 15px 0;
}

.filter__range-slider {
  margin: 0 14px 15px 0;
}

.filter__fieldset {
  margin: 0 0 15px;
  padding: 0;
  border: none;
}

.filter__category {
  margin: 0 0 6px 16px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
}

.filter__list {
  display: flex;
  margin: 0;
  padding: 10px 16px 9px;
  list-style: none;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 18.5px;
}

.filter__item:not(:last-child) .filter__label {
  margin-right: 20px;
}

.filter__label {
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}

.filter__radio + .filter__label::before {
  position: absolute;
  top: -2px;
  left: 0;
  content: "";
  width: 22px;
  height: 22px;
  background: url("../img/radio-off.svg") no-repeat left;
  background-size: 22px 22px;
  opacity: 0.8;
}

.filter__radio:not(:disabled) + .filter__label:hover::before {
  opacity: 1;
}

.filter__radio:checked + .filter__label::before {
  background: url("../img/radio-on.svg") no-repeat left;
  background-size: 22px 22px;
}

.filter__radio:disabled + .filter__label {
  opacity: 0.4;
  cursor: auto;
}

.filter__radio:focus + .filter__label {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

.filter__checkbox + .filter__label::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 23px;
  height: 20px;
  background: url("../img/checkbox-off.svg") no-repeat left;
  background-size: 20px 20px;
  opacity: 0.8;
}

.filter__checkbox:not(:disabled) + .filter__label:hover::before {
  opacity: 1;
}

.filter__checkbox:checked + .filter__label::before {
  background: url("../img/checkbox-on.svg") no-repeat left;
  background-size: 23px 20px;
}

.filter__checkbox:disabled + .filter__label {
  opacity: 0.4;
  cursor: auto;
}

.filter__checkbox:focus + .filter__label {
  outline: thin dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

.filter__button {
  margin: 21px 0 0 14px;
  width: 144px;
}

.sort__label {
  display: block;
  margin: 0 0 6px 16px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
}

.sort__box {
  position: relative;
}

.sort__icon {
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translateY(-50%);
  z-index: 1;
  fill: #ffffff;
}

.sort__select {
  display: block;
  padding: 10px 34px 9px 14px;
  margin: 0;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.2);
  border: none;
  border-radius: 18.5px;
  appearance: none;
  cursor: pointer;
}

.sort__select::-ms-expand {
  display: none;
}

.sort__select:hover {
  color: #333333;
}

.sort__select:hover + .sort__icon {
  fill: #333333;
}

.sort__option {
  color: #333333;
}

.range-slider__price {
  margin: 0 0 6px 16px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
}

.range-slider__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 220px;
  height: 37px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 18.5px;
}

.range-slider__inner {
  position: relative;
  z-index: 1;
  width: 156px;
  height: 4px;
  background-color: #d1dad3;
}

.range-slider__selected {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  content: "";
  width: 100%;
  height: 4px;
  background-color: #ffffff;
}

.range-slider__control {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 3;
  width: 20px;
  height: 20px;
  background-color: #ffffff;
  border-radius: 50%;
  cursor: pointer;
}

.range-slider__control::before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 4px;
  height: 4px;
  background-color: #ababab;
  border-radius: 50%;
}

.range-slider__control:hover {
  transform: translateY(-50%) scale(1.1);
}

.range-slider__control--left {
  left: -10px;
}

.range-slider__control--right {
  right: -10px;
}

.pagination {
  display: flex;
}

.pagination__list {
  display: flex;
  margin: 0 -10px 0 auto;
  padding: 0;
  list-style: none;
}

.pagination__link--inactive {
  opacity: 0.2;
}

.pagination__icon {
  fill: #ffffff;
}

.pagination__item {
  margin-right: 3px;
}

.pagination__link {
  display: block;
  min-width: 26px;
  padding: 5px 0 3px;
  font-size: 16px;
  line-height: 18px;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
}

.pagination__link:hover:not(.pagination__link--active):not(.pagination__link--inactive) {
  background-color: rgba(255, 255, 255, 0.2);
}

.pagination__link:active:not(.pagination__link--active):not(.pagination__link--inactive), .pagination__link:focus:not(.pagination__link--active):not(.pagination__link--inactive) {
  color: #333333;
  background-color: #ffffff;
}

.pagination__link:active:not(.pagination__link--active):not(.pagination__link--inactive) .pagination__icon, .pagination__link:focus:not(.pagination__link--active):not(.pagination__link--inactive) .pagination__icon {
  fill: #333333;
}

.pagination__link--active {
  color: #333333;
  background-color: #ffffff;
}

.pagination__arrow--prev {
